<template>
    <div class="device_page">
        <div>
            <div class="house">
                <i class="iconfont icon-fangzi"></i>
                <div class="msg">
                    <div class="hum">
                        <span class="val">{{eqp.humiditySet}}</span>
                        <span class="icon">
                            <i class="iconfont icon-shidu"></i>
                            <b>%</b>
                        </span>
                    </div>
                    <div class="tem">
                        <span class="val">{{eqp.temperature1}}</span>
                        <span class="icon">
                            <i class="iconfont icon-wendu"></i>
                            <b>&#8451;</b>
                        </span>
                    </div>
                </div>
            </div>
            <ul class="device_grid">
                <li @click="eqp_status(eqp.status)" :class="eqp.status ? '' : 'forbiden'">
                    <i class="iconfont icon-kaiguan"></i>
                    <span class="text">开</span>
                </li>
                <li @click="eqp_fanSpeed(eqp.fanSpeed)">
                    <template v-if="eqp.fanSpeed == 1">
                        <i class="iconfont icon-yijifeng"></i>
                        <span class="text">风速低</span>
                    </template>
                    <template v-else-if="eqp.fanSpeed == 2 || eqp.fanSpeed == 4">
                        <i class="iconfont icon-erjifeng"></i>
                        <span class="text">风速中</span>
                    </template>
                    <template v-else-if="eqp.fanSpeed == 3">
                        <i class="iconfont icon-sanjifeng"></i>
                        <span class="text">风速高</span>
                    </template>
                    <!-- <template v-else>
                        <i class="iconfont icon-wufeng"></i>
                        <span class="text">风速关</span>
                    </template> -->
                </li>
                <li @click="eqp_fanSpeed_auto(eqp.fanSpeed_auto)">
                    <template v-if="eqp.fanSpeed_auto">
                        <i class="iconfont icon-A"></i>
                        <span class="text">自动</span>
                    </template>
                    <template v-else>
                        <i class="iconfont icon-M"></i>
                        <span class="text">手动</span>
                    </template>
                </li>
                <li>
                    <i class="iconfont icon-neichushi"></i>
                    <span class="text">内除湿</span>
                </li>
                <li>
                    <i class="iconfont icon-shezhi"></i>
                    <span class="text">设置</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import yxMqtt from "@/utils/yxMqtt";

    export default {
        name: 'Device',
        data() {
            return {
                // mqtt 
                Mqtt: null,
                // 页面 功能
                eqp: {
                    status: 1,
                    fanSpeed_auto: false,
                    fanSpeed_last: 1,
                    fanSpeed: 1,
                    humiditySet_last: 0,
                    humiditySet: 80,
                    humidity: 0,
                    airFilter: 60,
                    temperature1: 18,
                    temperature2: 0,
                    errorCode: 0,
                    slaveNo: null,
                },
            }
        },
        created() {
            let _this = this;
            let Mqtt_dev = new yxMqtt({
                hostname: 'wss://13xt3d9.mqtt.iot.gz.baidubce.com:8884/mqtt',
                options: {
                    username: '13xt3d9/gh_0ddfb722a565_c9edd422aafdc95f',
                    password: 'xY9Y6MQUYdCvrnJK',
                    clientId: 'gh_0ddfb722a565_c9edd422aafdc95f',
                },
                topic: {
                    // sub: '$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/update/accepted',
                    sub: '$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/delta',
                    pub: '$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/update',
                },
                arrived(res) {
                    console.log(res);
                    let desired = res.message.desired;
                    _this.compute_arrrived_data(desired);
                },
                error(err) {
                    console.log(err);
                }
            });
            Mqtt_dev.mqttParam.options.clientId = 'gh_0ddfb722a565_c9edd422aafdc95f';
            this.Mqtt_dev = Mqtt_dev;
            this.Mqtt_dev.startSub();

            this.publish('开机', {
                "status": 1, // 开关机 状态
                "airFilter": 1000, // 滤网 剩余 使用时间
                "errorCode": 0, // 故障代码
                "fanSpeed": 1, // 风速
                "humidity": 80, // 当前湿度
                "humiditySet": 80, // 设置 湿度
                "temperature1": 18, // 当前 温度
                "temperature2": 15, // 盘管 温度
            });
        },
        methods: {
            // 处理 接收到的 数据
            compute_arrrived_data: function (reported) {
                // console.log(reported);
                if (('status' in reported) && 0 <= reported.status <= 1) { // 开关机 状态
                    this.eqp.status = reported.status;
                    this.publish(null, {
                        "status": reported.status
                    });
                }
                if ('fanSpeed' in reported && 1 <= reported.fanSpeed <= 4) { // 风速
                    this.eqp.fanSpeed = reported.fanSpeed;
                    this.eqp.fanSpeed_auto = reported.fanSpeed == 4 ? true : false;
                    this.publish(null, {
                        "fanSpeed": reported.fanSpeed
                    });
                }
                if ('humiditySet' in reported && 0 <= reported.humiditySet <= 100) { // 设置 湿度
                    this.eqp.humiditySet = reported.humiditySet;
                    this.eqp.humiditySet_last = reported.humiditySet;
                    this.publish(null, {
                        "humiditySet": reported.humiditySet,
                        "humidity": reported.humiditySet,
                    });
                }
                if ('humidity' in reported) { // 当前湿度
                    this.eqp.humidity = reported.humidity;
                }
                if ('airFilter' in reported) { // 滤网 剩余 使用时间
                    this.eqp.airFilter = reported.airFilter;
                }
                if ('temperature1' in reported) { // 当前 温度
                    this.eqp.temperature1 = reported.temperature1;
                }
                if ('temperature2' in reported) { // 盘管 温度
                    this.eqp.temperature2 = reported.temperature2;
                }
                if ('errorCode' in reported) { // 故障代码
                    // return false;
                    var errorCode = reported.errorCode
                    this.eqp.errorCode = errorCode;
                    if (errorCode == 0) {
                        return false;
                    }
                    var error_text = '';
                    switch (errorCode) {
                        case 1:
                            error_text = '盘管温度传感器故障（E1）';
                            break;
                        case 2:
                            error_text = '湿度传感器故障（E2）';
                            break;
                        case 3:
                            error_text = '排水保护故障（E0）';
                            break;
                        case 4:
                            error_text = '低压报警（E4）';
                            break;
                        case 5:
                            error_text = '高温报警，环境温度 > 41 &#8451;（E8）';
                            break;
                        default:
                            error_text = '未知故障（' + errorCode + '）';
                            break;
                    }
                    this.$toast({
                        message: error_text,
                        duration: 1500,
                    });
                }
                if ('slaveNo' in reported) { // 从机 地址
                    this.eqp.slaveNo = reported.slaveNo;
                }

                // 定时开关机 
                if ('timing' in reported) {
                    console.log('timing');
                    let timing = reported.timing;
                    let timing_pub = {
                        "timing_on": timing.timing_on,
                        "timing_start_hour": timing.timing_start_hour,
                        "timing_start_min": timing.timing_start_min,
                        "timing_end_hour": timing.timing_end_hour,
                        "timing_end_min": timing.timing_end_min,
                    }
                    this.publish(null, {timing: timing_pub});
                }
            },
            // 发送 数据
            publish(toast_text, payload) {
                if (this.eqp.status == 0 && !('status' in payload)) {
                    this.$toast({
                        message: '设备已关机',
                        duration: 1000,
                    });
                    return false;
                }
                this.Mqtt_dev.publish({
                    'reported': payload
                });
                if (toast_text != null) {
                    this.$toast({
                        message: toast_text,
                        duration: 1000,
                    });
                }
            },


            // 开关机 
            eqp_status(val) {
                val = val == 1 ? 0 : 1;
                let toast_text = val == 1 ? '开机' : '关机';
                this.publish(toast_text, {
                    "status": val
                });
                this.eqp.status = val;
            },
            // 设定 风速
            eqp_fanSpeed(val) {
                if (!this.eqp.status) return false;

                var toast_text = '自动模式无法控制风速';
                if (val != 4) {
                    val = ++val > 3 ? 1 : val;
                    toast_text = '风速 ' + val;
                    this.publish(toast_text, {
                        "fanSpeed": val
                    });
                    this.eqp.fanSpeed = val;
                } else {
                    this.$toast({
                        message: toast_text,
                        duration: 1000,
                    });
                }
            },
            // 设定 风机 自动
            eqp_fanSpeed_auto(val) {
                if (!this.eqp.status) return false;

                // 是否 切换为 自动
                this.eqp.fanSpeed_auto = !val;
                this.eqp.fanSpeed = !val ? 4 : this.eqp.fanSpeed_last;
                // console.log(this.eqp.fanSpeed_auto);
                // console.log(this.eqp.fanSpeed);
                if (!val) {
                    // this.eqp.fanSpeed_last = this.eqp.fanSpeed;
                    val = 4;
                } else {
                    val = this.eqp.fanSpeed_last;
                }
                let toast_text = val == 4 ? '自动' : '手动';
                this.publish(toast_text, {
                    "fanSpeed": val
                });
            },
        },
        beforeDestroy() {
            this.publish('关机', {
                'status': 0
            });
        },
        destroyed() {
            this.Mqtt_dev.closeSub();
        }
    }
</script>

<style scoped>
    .device_page {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        color: #fff;
    }

    .device_page>div {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100vmin;
        max-width: 600px;
        max-height: 600px;
        margin: 0;
        /* background: #000; */

        background: -webkit-radial-gradient(#165587, #2d4581, #383d7f);
        /* Safari 5.1 - 6.0 */
        background: -o-radial-gradient(#165587, #2d4581, #383d7f);
        /* Opera 11.6 - 12.0 */
        background: -moz-radial-gradient(#165587, #2d4581, #383d7f);
        /* Firefox 3.6 - 15 */
        background: radial-gradient(#165587, #2d4581, #383d7f);
        /* 标准的语法 */
    }

    .house {
        position: relative;
        flex: 1;
    }

    .house>i.iconfont {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        line-height: 100%;
        text-align: center;
        font-size: 5rem;
    }

    .house>.msg {
        position: absolute;
        top: 50%;
        left: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 2rem;
        height: 3rem;
        transform: translate(-40%, -36%);
    }

    .house>.msg>div {
        display: flex;
        align-items: center;
        padding: .2rem;
    }

    .house>.msg .val {
        width: .8rem;
        text-align: center;
        color: rgb(153, 255, 0);
        font-size: .5rem;
    }

    .house>.msg .icon {
        display: flex;
        width: .4rem;
        text-align: center;
        flex-direction: column;
        font-size: .38rem;
    }

    .house>.msg .icon>i {
        width: .4rem;
        height: .4rem;
        line-height: .4rem;
        border-radius: 50%;
        border: 1px solid rgba(255, 255, 255, .5);
        font-size: .30rem;
    }

    .device_grid {
        display: flex;
    }

    .device_grid>li {
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        padding: .1rem;
    }

    .device_grid>li.forbiden {
        color: #aaa;
    }

    .device_grid>li>.iconfont,
    .device_grid>li>.text {
        width: 100%;
        text-align: center;
    }

    .device_grid>li>.iconfont {
        font-size: .5rem;
    }

    .device_grid>li>.text {
        font-size: .30rem;
    }
</style>